@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
html {
  min-height: 100%;
}

body {
  margin: 0;
  font-family: "Space Mono", monospace;
  font-weight: 1000;
  background: linear-gradient(135deg, rgba(69, 72, 77, 1) 0%, rgba(0, 0, 0, 1) 100%);
  color: #05aed6;
}

.Tilt {
  width: 150px;
  height: 150px;
}

.logo {
  margin: 15%;
  width: 70%;
}

@media (max-width: 480px) {
  .Tilt {
    width: 50px;
    height: 50px;
  }
}

.form {
  width: 700px;
}

.input {
  width: 70%;
}

.btn {
  width: 25%;
  min-width: 105px;
}

.message {
  font-size: 1.5rem;
}

@media (max-width: 480px) {
  .message {
    font-size: 1rem;
  }
}

.title {
  font-size: 1.5rem;
}

.entries {
  font-size: 3rem;
}

@media (max-width: 480px) {
  .title {
    font-size: 1rem;
  }
  .entries {
    font-size: 2rem;
  }
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.border {
  border: 1px solid #05aed6;
}

button {
  cursor: pointer;
  background: none;
  color: #05aed6;
  border-radius: 5px;
}

.input-button {
  color: #05aed6;
}

input:focus {
  outline: none;
}

fieldset {
  color: #05aed6;
}

p {
  color: #05aed6;
}

